body {
  top: 0;
  height: 100vh;
  background: url() no-repeat center;
  background-size: cover;
  background-color: hsl(0, 0%, 16%);
}

.x {
  margin-left: 70px;
  margin-top: 70px;
  width: 40px;
  color: rgb(116, 116, 116);
  font-size: 40px;
  text-decoration-line: none;
}
.x:hover {
  color: black;
  font-weight: bold;
  font-size: 48px;
  margin-bottom: -9px;
  cursor: pointer;
}
.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.detail {
  background-color: white;
  margin: 70px;
  font-weight: 300;
  padding: 40px;
  border-radius: 5px;
  display: grid;
  grid-template-columns: minmax(150px, 1fr) 2fr;
  grid-gap: 10px;
  text-decoration: none;
  color: inherit;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie__img {
  position: relative;
  justify-content: center;
  max-width: 250px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie__title,
.movie__year {
  margin: 0;
  font-weight: 300;
  text-decoration: none;
}

.movie__title {
  margin-bottom: 15px;
  font-size: 40px;
  font-weight: bold;
  color: #2c2c2c;
  text-decoration: none;
}

.movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0px;
}

.movie__genres li,
.movie__year {
  margin-right: 10px;
  font-size: 20px;
}
